let Token = uni.getStorageSync('USERS_KEY').token;
const URL = 'http://pandla.io:8080/v1/';
const systemUrl = URL + 'system/';
const fansUrl = URL + 'fans/';
const dynUrl = URL + 'dyn/';
const pollUrl = URL + 'poll/';
const panUrl = URL + 'pan/';
const forceUrl = URL + 'force/';
const treatyUrl = URL + 'tre/';
const barUrl = URL + 'bar/';
const invUrl = URL + 'inv/';

/************************************************/
// 
export const login = (account, password) => {
	uni.request({
		url: systemUrl + 'login',
		method: 'POST',
		data: {
			name: account,
			password: password,
		},
		header: {
			'content-type': 'application/json'
		},
		success: (res) => {
			// console.log(res.data.data);
			if (res.data.status == 200) {
				uni.setStorageSync('USERS_KEY', res.data.data);
				Token = uni.getStorageSync('USERS_KEY').token;
				uni.reLaunch({
					url: '../main/main'
				});
			} else {
				uni.showToast({
					icon: 'none',
					title: res.data.msg
				});
			}
		},
		fail: (err) => {
			uni.showToast({
				icon: 'none',
				title: ''
			});
		}
	});
};

// 
export const sendCode = (account, regCodeBtn, timerId) => {
	uni.request({
		url: systemUrl + 'sendCode',
		data: {
			parame: account,
		},
		success() {
			uni.showToast({
				icon: 'none',
				title: ''
			})
			regCodeBtn.btnStatus = true
			timerId = setInterval(() => {
				var codeTime = regCodeBtn.codeTime;
				codeTime--;
				regCodeBtn.codeTime = codeTime;
				regCodeBtn.text = "(" + codeTime + ")";
				if (codeTime < 1) {
					clearInterval(timerId);
					regCodeBtn.text = "";
					regCodeBtn.codeTime = "60";
					regCodeBtn.btnStatus = false;
				}
			}, 1000);
			return false;
		},
		fail() {
			uni.showToast({
				icon: 'none',
				title: ''
			});
		}
	});
};

// 
export const register = (password, account, regCode, inviteCode) => {
	uni.request({
		url: systemUrl + 'register',
		method: 'POST',
		data: {
			password: password,
			account: account,
			regCode: regCode,
			inviteCode: inviteCode
		},
		header: {
			'content-type': 'application/json'
		},
		success: (res) => {
			// console.log(res.data.status);
			if (res.data.status == 200) {
				uni.showToast({
					icon: 'none',
					title: ''
				});
				uni.setStorageSync('USERS_KEY', res.data.data);
				uni.reLaunch({
					url: '../user/update'
				});
				// console.log(res.data);
			} else {
				uni.showToast({
					icon: 'none',
					title: res.data.msg
				})
				return;
			}
		},
		fail: (err) => {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return;
		}
	});
};

