// 
export const upPicture = (userId) => new Promise((resolve, reject) => {
	uni.chooseImage({
		count: 1,
		sizeType: ['compressed'], // 
		sourceType: ['album', 'camera'], // 
		success(chooseImageRes) {
			const tempFilePaths = chooseImageRes.tempFilePaths;
			const uploadTask = uni.uploadFile({
				url: systemUrl + 'pic/upPicture?id=' + userId,
				filePath: tempFilePaths[0],
				name: 'file',
				success: (res) => {
					console.log(res.data);
					var jsonObj = JSON.parse(res.data);
					console.log('uploadImage success, res is:', jsonObj.data);
					uni.showToast({
						title: '',
						icon: 'success',
						duration: 1000
					});
					resolve(jsonObj.data);
					// avatar = jsonObj.data.id;
				},
				fail: (err) => {
					console.log('uploadImage fail', err);
					uni.showToast({
						icon: 'none',
						title: ',4M'
					});
				}
			});
			uploadTask.onProgressUpdate((res) => {
				console.log('' + res.progress);
				console.log('' + res.totalBytesSent);
				console.log('' + res.totalBytesExpectedToSend);
			});
		}
	});
});

