const pReg = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/;
const eReg = /^\w+@\w+.[a-zA-Z]{2,3}(.[a-zA-Z]{2,3})?$/;

// 
export const phoneValidate = (phone) => {
	if (phone.length == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if (!pReg.exec(phone) || phone.length < 8) {
		/*  ^([6|9])\d{7}$
			^[0][9]\d{8}$
			^[1][3-8]\d{9}$
			^[1][3-8]\d{9}$|^([6|9])\d{7}$|^[6]([8|6])\d{5}$ */
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	return true;
};

// 
export const emailValidate = (email) => {
	if (email.length == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if(!eReg.exec(email)) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	return true;
};

// 
export const joinValidate = (account) => {
	if (account.length == "") {
		uni.showToast({
			icon: 'none',
			title: '/'
		});
		return false;
	} else {
		if (!pReg.exec(account) && !eReg.exec(account)) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	return true;
};

// 
export const codeValidate = (regCode) => {
	if (regCode.length == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if (regCode.length < 6) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	return true;
};

// 
export const pwdValidate = (password, verifyPwd) => {
	if (password.length == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if (password.length < 6) {
			uni.showToast({
				icon: 'none',
				title: '6'
			});
			return false;
		}
		if(verifyPwd != password ) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	return true;
};

// 
export const userValidate = (userInfo) => {
	if (userInfo.name.length < 2) {
	    uni.showToast({
	        icon: 'none',
	        title: '2'
	    });
	    return false;
	}
	if(userInfo.stature == '') {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if(userInfo.stature<50 ||userInfo.stature>300) {
			uni.showToast({
				icon: 'none',
				title: '(cm):50~300'
			});
			return false;
		}
	}
	if(userInfo.weight == '') {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if(userInfo.weight<5 || userInfo.weight>700) {
			uni.showToast({
				icon: 'none',
				title: '(kg):5~700'
			});
			return false;
		}
	}
	if(userInfo.age == '') {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if(userInfo.age<18 ||userInfo.age>150) {
			uni.showToast({
				icon: 'none',
				title: ':18~150'
			});
			return false;
		}
	}
	if(userInfo.site == '') {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	if(userInfo.signature == '') {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	return true;
};

// 
export const barValidate = (barInfo, isUpload) => {
	if (barInfo.name == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	if (barInfo.phone.length == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if (!pReg.exec(barInfo.phone) || barInfo.phone.length < 8) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	if (barInfo.location == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	if (barInfo.intro == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	if (barInfo.username == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	}
	if (barInfo.userphone == "") {
		uni.showToast({
			icon: 'none',
			title: ''
		});
		return false;
	} else {
		if (!pReg.exec(barInfo.userphone) || barInfo.userphone.length < 8) {
			uni.showToast({
				icon: 'none',
				title: ''
			});
			return false;
		}
	}
	if (isUpload == false) {
		uni.showToast({
			icon: 'none',
			title: 'LOGO'
		});
		return false;
	}
	return true;
};
